<template>
	<div class="user">
		<div class="photo">
			<img src="static/img/faces/face-1.jpg"/>
		</div>
		<div class="info">
			<a data-toggle="collapse" @click="toggleMenu" href="javascript:void(0)">
				<span>
					{{ account.username }}
					<b class="caret"></b>
				</span>
			</a>
			<div class="clearfix"></div>
			<div>
				<el-collapse-transition>
					<ul class="nav" v-show="!isClosed">
						<li>
							<a href="#" @click.prevent="onLogout">
								<span class="sidebar-mini">В</span>
								<span class="sidebar-normal">Выйти</span>
							</a>
						</li>
					</ul>
				</el-collapse-transition>
			</div>
		</div>
	</div>
</template>
<script>
	import CollapseTransition from 'element-ui/lib/transitions/collapse-transition'
	import swal from 'sweetalert2'
	
	export default {
		components: {
			[CollapseTransition.name]: CollapseTransition
		},
		data () {
			return {
				isClosed: true
			}
		},
		computed: {
			account () {
				return this.$store.state.app.account
			}
		},
		methods: {
			toggleMenu () {
				this.isClosed = !this.isClosed
			},
			onLogout: function () {
				var self = this
				swal({
					title: 'Выход',
					text: 'Выход пользователя из системы...',
					onOpen: () => {
						swal.showLoading()
						self.$store.dispatch('userLogout', {
							callback: function () {
								self.$router.push({ path: '/login' })
								swal.close()
							}
						})
					},
					showConfirmButton: false,
					allowOutsideClick: false,
					allowEscapeKey: false,
					allowEnterKey: false
				})
			}
		}
	}
</script>
<style>
	.collapsed {
		transition: opacity 1s;
	}
</style>



