<template>
	<nav class="navbar navbar-default">
		<div class="container-fluid">
			<div class="navbar-minimize">
				<button class="btn btn-fill btn-icon" @click="minimizeSidebar">
					<i v-bind:class="$sidebar.isMinimized ? 'ti-menu-alt' : 'ti-more-alt'"></i>
				</button>
			</div>
			<div class="navbar-header">
				<button type="button" class="navbar-toggle" v-bind:class="{toggled: $sidebar.showSidebar}" @click="toggleSidebar">
					<span class="sr-only">Переключить навигацию</span>
					<span class="icon-bar bar1"></span>
					<span class="icon-bar bar2"></span>
					<span class="icon-bar bar3"></span>
				</button>
				<a class="navbar-brand">{{this.$route.name}}</a>
				<a @click="getMoney" class="navbar-brand not-display  cur-pointer">{{ moneyValue }} {{ moneyUnit }}</a>
			</div>
			<div class="collapse navbar-collapse">
				<ul class="nav navbar-nav navbar-right">
					<li>
						<a @click="getMoney" class="navbar-brand cur-pointer">{{ moneyValue }} {{ moneyUnit }}</a>
					</li>
					<!-- <li>
					<router-link to="/admin/overview" class="btn-rotate">
						<i class="ti-settings"></i>
					</router-link>
					</li> -->
				</ul>
			</div>
		</div>
	</nav>
</template>
<script>
	import swal from 'sweetalert2'

	export default {
		data () {
			return {
				activeNotifications: false
			}
		},
		methods: {
			capitalizeFirstLetter (string) {
				return string.charAt(0).toUpperCase() + string.slice(1)
			},
			toggleNotificationDropDown () {
				this.activeNotifications = !this.activeNotifications
			},
			closeDropDown () {
				this.activeNotifications = false
			},
			toggleSidebar () {
				this.$sidebar.displaySidebar(!this.$sidebar.showSidebar)
			},
			hideSidebar () {
				this.$sidebar.displaySidebar(false)
			},
			minimizeSidebar () {
				this.$sidebar.toggleMinimize()
			},
			joinNewRoom () {
				this.$store.dispatch('socket_room_join', this.room)
			},
			getMoney () {
				this.$store.dispatch('shopInitUesrMoney', {
					data: {},
					callback: function (data) {
					},
					successFalse: function (data) {
						var error = (data.message === 'Failed to fetch')
							? 'Невозможно сделать запрос, нет доступа к серверу или интернету' : data.message
						swal({
							title: 'Ошибка',
							text: error,
							type: 'error',
							showConfirmButton: true
						})
					}
				})
			}
		},
		computed: {
			moneyValue () {
				return this.$store.state.shop.money.value
			},
			moneyUnit () {
				return this.$store.state.shop.money.unit
			},
			accountId () {
				return this.$store.state.app.account.id
			},
			moneyCount () {
				return this.$store.state.soket.moneyCount
			},
			room () {
				return 'usermoney:' + this.accountId
			}
		},
		watch: {
			accountId () {
				this.joinNewRoom()
			},
			moneyCount (value) {
				this.$store.commit('changeMoneyValue', value.item)
				// this.$store.commit('changeMoneyValue', value[0].item)
			}
		},
		mounted () {
			this.getMoney()
			this.joinNewRoom()
		},
		beforeDestroy () {
			this.$store.dispatch('socket_room_leave', this.room)
		}
	}

</script>
<style>
@media (min-width: 992px) {
	.not-display {
		display: none;
	}
}
</style>



